.App {
  text-align: center;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.exciting {
  display: flex;
  justify-content: space-between;
}
.box {
  /* height: 611px; */
  width: 643px;
  left: 1456px;
  top: 144px;
  border-radius: 0px;
}
.excitinag {
  background: #e9f2f9;
  width: 480px;
  height: 73px;
  padding: 3px;
  /* opacity: 0.1; */
  color: #247cc0;
}
h1 {
  font-size: 50px;
  font-family: Hind Silieuri;
  margin: 10px 0;
}
.span1 {
  color: #030d15;
  font-weight: 800;
  font-size: 20px;
  margin-right: 10px;
}
.span2 {
  text-decoration: line-through;
}
.b > p {
  margin-bottom: 20px;
}
.b {
  display: flex;
  flex-direction: column;
  padding: 30px 0 0 80px;
}
.button {
  width: 150px;
  height: 50px;
  margin-right: 10px;
  background: #247cc0;
  border: 0;
  color: #fff;
  cursor: pointer;
}
.box > img {
  width: 100%;
}
.Favorite {
  color: #c72535;
  margin-bottom: 0;
  text-transform: uppercase;
}
.menyo {
  display: flex;
  background: #fff;
  justify-content: space-between;
}
.menyo img {
  height: 100%;
}
.uli {
  list-style: none;
  display: flex;
}
.uli li a {
  text-decoration: none;
  text-transform: uppercase;
  margin: 0 10px;
  color: black;
  border-bottom: 3px solid transparent;
  transform: 0.9s;
}
.uli > li {
  display: flex;
  align-items: center;
}

.uli li a:hover {
  border-bottom: 3px solid black;
}
.ikoka {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.ikoka > div {
  margin: 0 10px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.Rectangle {
  height: 237px;
  background: #ffffff;
  box-shadow: 0px 100px 80px rgba(174, 174, 174, 0.08),
    0px 43.751px 36.4414px rgba(174, 174, 174, 0.0575083),
    0px 19.8401px 19.1406px rgba(174, 174, 174, 0.0476886),
    0px 6.77214px 9.61331px rgba(174, 174, 174, 0.04),
    0px 0.190079px 4.14347px rgba(174, 174, 174, 0.0323114),
    0px -1.73968px 1.20532px rgba(174, 174, 174, 0.0224916);
  display: flex;
  padding: 76px 118px;
}
.Rectangle > div {
  height: 85.00086212158203px;
  width: 904px;
  display: flex;
  justify-content: center;
}
.Recent_Product {
  padding: 110px 0 0 0;
  display: flex;
}
.Recent_Product > div {
  position: relative;
  width: 25%;
}
.Recent_Product img {
  width: 100%;
}
.Shopping {
  position: absolute;
  right: -20px;
  top: -21px;
  border: 2px solid #f1f1f1;
  border-radius: 50%;
  display: flex;
  width: 44px;
  height: 44px;
  align-items: center;
  justify-content: center;
}
.Shopping1 {
  border-radius: 50%;
  display: flex;
  width: 44px;
  height: 44px;
  background: #f3f8fb;
  align-items: center;
  justify-content: center;
}
.div1 {
  display: flex;
  background: #247cc0;
}
h2 {
  font-family: Hind Siliguri;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 40px;
  color: #030d15;
}
.standard {
  padding: 30px 15px;
}
.standard > p {
  padding-bottom: 20px;
}
.StarIcon {
  margin-top: 10px;
}
.StarIcon1 {
  color: #c72535;
}
.StarIcon11 {
  color: #f4f9fc;
}
.SpecialOffer {
  width: 100%;
  height: 333.08px;
  margin-top: 160px;
  display: flex;
}

.background {
  width: 50%;
  position: relative;
  margin: 0 15px;
  height: 296px;
  background: linear-gradient(257.26deg, #008bf0 -13.2%, #d943c9 130.95%);
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.today {
  width: 228px;
  height: 147px;
  text-transform: uppercase;
  position: absolute;
  top: 67px;
  right: 75px;
  bottom: 271px;
  left: 40px;
  color: #fff;
}
.today > p {
  font-size: 14px;
}
.today > h2 {
  color: #fff;
}

.width1 {
  position: absolute;
  right: -21px;
  transform: rotate(8deg);
}
.c {
  transform: rotate(-8deg);
